<template>
  <div class="right-main">
    <div class="outer">
      <div class="inner">
        <div class="row">
          <el-button type="small" @click="getID">{{language.GenerateTagID}}</el-button>
        </div>
        <div class="row">
          <el-input :placeholder="language.tagIdPH" v-model="tagIdInp" class="inp">
            <template slot="prepend">ID</template>
          </el-input>
        </div>
        <div class="border-bottom"></div>
        <div class="upload-title">{{language.makeTitle}}</div>
        <span slot="tip" class="el-upload__tip">{{language.makeTip}}</span>
        <div class="tableBox">
          <el-table :data="tableData" border style="width: 100%">
            <el-table-column prop="id" label="ID" width="400">
            </el-table-column>
            <el-table-column :label="language.file">
              <template scope="scope">
                <el-upload class="upload" v-if="scope.row.show" :action="action" :on-success="uploadSuccess" :data="{uuid: scope.row.id}">
                  <el-button slot="trigger" type="text">{{language.upload}}</el-button>
                </el-upload>
                <span class="c67c23a" v-if="!scope.row.show">{{language.uploadSucMes}}</span>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {
          GenerateTagID: '生成标记ID',
          tagIdPH: '请拷贝生成的ID进行制作',
          makeTitle: '待上传线下标记文件',
          makeTip: '上传线下制作完成的文件，需对应所嵌入的ID进行上传，成功后可实现追踪。',
          file: '文件',
          upload: '上传',
          uploadSucMes: '上传成功',
        },
        tagIdInp: '',
        tableData: [],
        action: ''
      };
    },
    methods: {
      // 获取ID
      getID() {
        const vm = this;
        this.$httpi.get('/wyfx/file/getUuid')
          .then(function (response) {
            if (response.data.code == '200') {
              vm.tableData.push({
                'id': response.data.msg,
                'show': true
              });
              vm.tagIdInp = response.data.msg;
            }
          })
      },
      uploadSuccess(response, file, fileList) {
        const vm = this;
        for (let i in this.tableData) {
          if (this.tableData[i].id == response.msg) {
            this.tableData[i].show = false;
          }
        }
      }
    },
    mounted() {
      const vm = this;
      vm.action = vm.$rootPath + '/wyfx/file/upload';
      // 獲取文本信息
      this.$httpi.get('/wyfx/page/getPageMsg', {
          params: {
            msg: 'OfflineMake'
          }
        })
        .then(function (response) {
          if (response.data.code == '200') {
            this.language = response.data.data;
          }
        })
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .right-main {
    height: calc(100% - 20px);
    padding: 10px;
  }

  .outer {
    width: 100%;
    height: 100%;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }

  .inner {
    width: calc(100% - 40px);
    height: calc(100% - 40px);
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
  }

  .row {
    margin-bottom: 10px;
  }

  .border-bottom {
    border-bottom: 1px solid rgb(223, 231, 236);
  }

  .inp {
    width: 400px;
  }

  .upload-title {
    margin-top: 10px;
  }

  .tableBox {
    width: 600px;
    height: calc(100% - 145px);
    margin-top: 10px;
    overflow: auto;
  }

</style>



